  outline: inherit;
  background: none;
  border: none;
  padding: 0;
}

button:active {
  color: #242628;
  background-color: #00fede;
}

.load-more-button {
  margin: 0 auto 120px;
  display: block;
}

.load-more-hidden {
  display: none;
}

.submit-button, .load-more-button {
  color: #00fede;
  background-color: #242628;
  width: 150px;
  height: 50px;
  font-size: 140%;
}

.header {
  justify-content: center;
  align-items: center;
  display: flex;
}

.search {
  background-color: #242628;
  width: 100px;
  height: 100px;
  margin: 40px;
  font-family: Tektur;
  transition: all .5s;
  position: relative;
  overflow: hidden;
}

.search:before {
  content: "";
  background-color: #00fede;
  width: 3px;
  height: 100%;
  transition: all .5s;
  display: block;
  position: relative;
}

.search.open {
  width: 420px;
}

.search.open:before {
  height: 60px;
  margin: 20px 0 20px 30px;
  position: absolute;
}

.search-box {
  box-shadow: none;
  color: #fff;
  background: none;
  border: none;
  width: 100%;
  height: 100%;
  padding: 20px 100px 20px 45px;
  font-family: Tektur;
  font-size: 140%;
}

.search-box:focus {
  outline: none;
}

.search-button {
  cursor: pointer;
  width: 100px;
  height: 100px;
  padding: 20px;
  display: block;
  position: absolute;
  top: 0;
  right: 0;
}

.search-icon {
  border: 3px solid #00fede;
  border-radius: 40px;
  width: 40px;
  height: 40px;
  margin-left: 5px;
  transition: all .5s;
  display: block;
  position: relative;
}

.search-icon:before {
  content: "";
  background-color: #00fede;
  width: 3px;
  height: 15px;
  transition: all .5s;
  display: block;
  position: absolute;
  top: 30px;
  right: -2px;
  transform: rotate(-45deg);
}

.search-icon:after {
  content: "";
  background-color: #00fede;
  width: 3px;
  height: 15px;
  transition: all .5s;
  display: block;
  position: absolute;
  top: 40px;
  right: -12px;
  transform: rotate(-45deg);
}

.open .search-icon {
  border-radius: 60px;
  width: 60px;
  height: 60px;
  margin: 0;
}

.open .search-icon:before {
  height: 18px;
  top: 23px;
  right: 22px;
  transform: rotate(52deg);
}

.open .search-icon:after {
  height: 18px;
  top: 13px;
  right: 22px;
  transform: rotate(-230deg);
}

